body{
  font-family: sans-serif;
  font-size: 15px;
  padding: 67px;
  font-weight: 300;
  letter-spacing: 1px;
  box-sizing: border-box;
  word-spacing: 3px;
  background: #ffda6b;
}


.trigger {
  position: absolute;
  opacity: 1;
  transition: 0.1s;
  /* width: auto;
  height: auto; */
}
.trigger-hidden {
  opacity: 0;
  visibility: hidden;
}